.fenty-section {
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.05em;
  width: 100%;
  margin: 140px 0 0 0;
  padding-left: 40px;
  cursor: pointer;

  h2 {
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  p {
    font-size: 15px;
    font-weight: 500;
    color: #555;
    margin-bottom: 40px;
  }

  .swiper {
    padding-bottom: 50px;
  }

  .fenty-card {
    text-align: center;

    img {
      width: 534.41px;
      height: 374.08px;
      object-fit: cover;
      display: block;
    }

    .fenty-text {
      cursor: pointer;
      margin-top: 30px;

      .date {
        display: block;
        font-size: 14px;
        font-weight: 500;
        color: #333;
        margin-bottom: 15px;
      }

      h3 {
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 8px;
      }

      p {
        font-size: 14px;
        color: #555;
        margin-bottom: 10px;
        line-height: 1.4;
      }

      a {
        font-size: 13px;
        color: #000;
        font-weight: 600;
        text-decoration: underline;
      }
    }
  }

  /* ÌôîÏÇ¥Ìëú Î≤ÑÌäº */
  .swiper-button-prev,
  .swiper-button-next {
    width: 36px;
    height: 36px;
    border: 1px solid #000;
    border-radius: 50%;
    background: #fff;
    top: -40px;
    color: #000;
    z-index: 20;

    &:after {
      font-size: 14px;
      font-weight: bold;
    }
  }

  .swiper-button-prev {
    right: 60px;
    left: auto;
  }

  .swiper-button-next {
    right: 20px;
  }

  /* Ïä§ÌÅ¨Î°§Î∞î */
  .swiper-scrollbar {
    background: #eee;
    height: 4px;
    border-radius: 2px;

    .swiper-scrollbar-drag {
      background: #000;
    }
  }
}
