.brand-section {
  background-color: #eecdb7; // ÏÇ¥Íµ¨Îπõ Î∞∞Í≤Ω
  text-align: center;
  margin: 140px 0 0 0;
  padding: 80px 20px;

  h2 {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 30px;
  }

  p {
    font-size: 15px;
    margin-bottom: 40px;
    font-weight: 500;
    color: #333;
  }

  .brand-list {
    display: flex;
    justify-content: center;
    gap: 15px;
    flex-wrap: wrap; // Î∞òÏùëÌòï ÎåÄÏùë
    margin: 0 auto;
  }

  .brand-card {
    flex: 1;
    max-width: 332px;
    cursor: pointer;
    text-align: center;

    img {
      width: 332px;
      height: auto;
      display: block;
    }

    h3 {
      margin-top: 12px;
      line-height: 24px;
      font-size: 18px;
      font-weight: 700;
      
    }
  }
}
