.footer {
  background: #000;
  color: #fff;
  font-family: "Roboto";
  display: flex; // Ï¢å/Ïö∞ Î∞∞Ïπò
  justify-content: space-between;
  gap: 40px;

  a {
    color: #fff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer-left {
    padding: 40px 40px;
    background: #1f1f1f;
    position: relative;

    .subscribe {
      margin-bottom: 40px;

      h3 {
        font-size: 18px;
        font-weight: 700;
      }

      p {
        font-size: 11px;
        margin: 15px 0;
      }

      .input-box {
        display: flex;
        margin: 10px 0;

        input {
          padding: 10px;
          background: none;
          border: 1px solid #fff;
          width: 450px;
          border-radius: 5px;
        }

        .btn1 {
          width: 50px;
          border: none;
          background: none;
          color: #fff;
          position: absolute;
          cursor: pointer;
          left: 440px;
          top: 114px;
        }
        .btn2 {
          width: 50px;
          border: none;
          background: none;
          color: #fff;
          position: absolute;
          cursor: pointer;
          left: 440px;
          top: 162px;
        }
      }

      .terms {
        letter-spacing: 0;
        font-size: 10px;
        color: #aaa;
        margin-top: 15px;
        line-height: 1.4;
        font-weight: 600;
      }
      .terms a {
        text-decoration: underline;
      }
    }

    .footer-logo {
      height: 35px;
      position: absolute;
      bottom: 40px;
    }
  }

  .footer-right {
    flex: 2;
    display: flex;
    flex-direction: column; // ‚úÖ ÏúÑ/ÏïÑÎûò Î∞∞Ïπò
    margin-top: 40px;
    gap: 40px;

    /* Í≥†Í∞ùÏÑºÌÑ∞ + About ÎÇòÎûÄÌûà */
    .footer-right-top {
      display: flex;
      justify-content: space-between;
      gap: 40px;

      .customer-service {
        h4{
          margin-bottom: 15px;
        }
        p {
          font-size: 11px;
          margin-bottom: 15px;
        }

        ul {
          list-style: none;
          padding: 0;

          li {
            margin-bottom: 8px;
            font-size: 11px;
            line-height: 24px;
          }
        }
      }

      .about {
        flex: 1.2;
        display: flex;
        gap: 20px;
        align-items: flex-start;

        .about-text {
          flex: 2;

          h4 {
            font-size: 13px;
            font-weight: 700;
            margin-bottom: 15px;
          }

          p {
            font-size: 11px;
            margin-bottom: 15px;
          }

          ul {
            list-style: none;
            padding: 0;

            li {
              margin-bottom: 8px;
              font-size: 11px;
              line-height: 23px;
            }
          }
        }

        .about-img {
          border-radius: 8px;
          width: 274px;
          height: 256px;
          margin-right: 40px;
        }
      }
    }

    /* ‚úÖ bottom Î∂ÑÎ¶¨ */
    .footer-bottom {
      display: block;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      gap: 20px;
      padding: 20px 0;

      .social {
        display: flex;
        gap: 12px;
        margin-bottom: 40px;

        img {
          width: 22px;
          height: 22px;
          object-fit: contain;
          cursor: pointer;
          transition: opacity 0.2s ease;

          &:hover {
            opacity: 0.7;
          }
        }
      }

      .footer-bottom-bottom{
        display: flex;
      }
      .country {
        display: flex;
        align-items: center;
        gap: 8px;

        .flag {
          width: 20px;
        }

        span {
          font-size: 14px;
        }
      }

      .footer-links {
        display: flex;
        flex-wrap: wrap;
        gap: 15px;

        a {
          font-size: 12px;
          color: #aaa;
        }
      }
    }
  }
}
